// й ц [у] [к] [е] н г ш щ з [х] ъ ф ы в [а] п [р] [о] л д ж э я ч [с] м [и] [т] ь б ю
//

export const letterConvert = (letter: string): string => {
    if (letter === "е") { return "e" }
    if (letter === "о") { return "o" }
    if (letter === "и") { return "u" }
    if (letter === "у") { return "y" }
    if (letter === "р") { return "ρ" }
    if (letter === "а") { return "a" }
    if (letter === "к") { return "κ" }
    if (letter === "х") { return "x" }
    if (letter === "с") { return "c" }
    if (letter === "т") { return "τ" }

    return letter
}
